<template>
    <div class="amap-page-container">
        <el-amap vid="amapDemo" :center="center" :amap-manager="amapManager" :zoom="zoom" :plugin="plugin" class="amap-demo">
        </el-amap>

        <div class="toolbar">
        <span v-if="loaded">
          location: lng = {{ lng }} lat = {{ lat }}
        </span>
            <span v-else>正在定位</span>
        </div>
    </div>
</template>

<style>
    .amap-page-container {
        height: 400px;
    }
</style>

<script>

    import VueAMap from 'vue-amap';

    VueAMap.initAMapApiLoader({
        key: '911fe5dcb6e74da0011d2c27df758b5c',
        plugin: ['Autocomplete', 'PlaceSearch', 'Scale', 'OverView', 'ToolBar', 'Geolocation', 'PolyEditor', 'AMap.CircleEditor']
    });

    let amapManager = new VueAMap.AMapManager();
    export default {
        data() {
            let self = this;
            return {
                center: [121.59996, 31.197646],
                lng: 0,
                lat: 0,
                loaded: false,
                amapManager,
                zoom: 15,
                plugin: [{
                    pName: 'Geolocation',
                    events: {
                        init(o) {
                            // o 是高德地图定位插件实例
                            o.getCurrentPosition((status, result) => {
                                if (result && result.position) {
                                    self.lng = result.position.lng;
                                    self.lat = result.position.lat;
                                    self.center = [self.lng, self.lat];
                                    self.loaded = true;
                                    self.$nextTick();
                                }
                            });
                        }
                    }
                }]
            };
        }
    };
</script>


